import {
  FunctionComponent,
  forwardRef,
  PropsWithoutRef,
  Ref,
  useState,
  useCallback,
  useEffect,
  useMemo,
  ElementType,
  useContext,
  useRef,
  FC,
  isValidElement
} from 'react';

import Flex from '../Flex';
import Link from '../Link';
import Count from '../Badges/Count';
import Icon, { registerIcon } from '../Icon';
import * as timesIcon from '../Icon/icons/times.icon';
import * as plusIcon from '../Icon/icons/plus.icon';
import * as caseSolidIcon from '../Icon/icons/case-solid.icon';
import * as barsIcon from '../Icon/icons/bars.icon';
import * as caretLeftIcon from '../Icon/icons/caret-left.icon';
import * as caretRightIcon from '../Icon/icons/caret-right.icon';
import Image from '../Image';
import type { ForwardProps } from '../../types';
import {
  useI18n,
  useBreakpoint,
  useConsolidatedRef,
  useDirection,
  useTheme,
  useElement,
  useTransitionState
} from '../../hooks';
import { openCloseStates } from '../../hooks/useTransitionState';
import Grid from '../Grid';
import Button from '../Button';
import Drawer from '../Drawer';
import Backdrop from '../Backdrop';
import Tooltip from '../Tooltip/Tooltip';
import SkipLinks, { SkipLinksProps } from '../SkipLinks';
import VisuallyHiddenText from '../VisuallyHiddenText';

import AppShellContext, { AppShellDrawerContext } from './AppShellContext';
import AppShellDrawer from './Drawer';
import AppHeader from './AppHeader';
import {
  StyledAppNav,
  StyledAppInfo,
  StyledCaseTypes,
  StyledScrollWrap,
  StyledUtils,
  StyledAppMain,
  StyledAppShellNavIcon,
  StyledNavCasesList,
  StyledAppEnvironment,
  StyledBannerRegion,
  StyledCountIcon,
  StyledUtilIconCount,
  StyledMobileNav,
  StyledAppShellTooltip,
  StyledAppShellToggleButton
} from './AppShell.styles';
import {
  AppShellProps,
  CaseProps,
  CaseTypeProps,
  LinkProps,
  NavListItemProps,
  UtilItemProps,
  UtilsProps
} from './AppShell.types';
import NavigationList from './NavigationList';
import { AppShellSearch } from './AppShellSearch';

registerIcon(timesIcon, plusIcon, caseSolidIcon, barsIcon, caretRightIcon, caretLeftIcon);

const CaseTypes: FunctionComponent<{ caseTypes: CaseTypeProps[] }> = ({
  caseTypes
}: {
  caseTypes: CaseTypeProps[];
}) => {
  const t = useI18n();

  const transformedCaseTypes = caseTypes.map(caseType => {
    return {
      ...caseType,
      primary: caseType.name
    };
  });

  return useMemo(
    () => (
      <StyledCaseTypes>
        <NavigationList
          items={[
            {
              primary: t('app_shell_create'),
              visual: <Icon name='plus' as={StyledAppShellNavIcon} />,
              items: transformedCaseTypes
            }
          ]}
        />
      </StyledCaseTypes>
    ),
    [caseTypes]
  );
};

const Links: FunctionComponent<{ links: LinkProps[] }> = (props: { links: LinkProps[] }) => {
  const { links } = props;
  const [transformedLinks, setTransformedLinks] = useState<NavListItemProps[]>([]);

  // TODO: Can this be a memo instead of a state and an effect?
  const mergedNavLinks = (items: LinkProps[]): NavListItemProps[] => {
    return items.map((link: LinkProps) => {
      return {
        ...link,
        primary: link.name,
        'aria-current': link.active ? 'page' : null,
        visual: link.icon ? <Icon name={link.icon} as={StyledAppShellNavIcon} /> : null,
        onClick: link.links ? undefined : link.onClick,
        items: link.links ? mergedNavLinks(link.links) : undefined
      };
    });
  };

  useEffect(() => {
    setTransformedLinks(mergedNavLinks(links));
  }, [links]);

  return (
    <div>
      <NavigationList items={transformedLinks} />
    </div>
  );
};

const Cases: FunctionComponent<{ cases: CaseProps[] }> = (props: { cases: CaseProps[] }) => {
  const { cases } = props;

  const setupCases = (items: CaseProps[]): NavListItemProps[] => {
    return items.map((item: CaseProps) => {
      const {
        id,
        primary,
        secondary,
        visual = <Icon name='case-solid' as={StyledAppShellNavIcon} />,
        active,
        onClick,
        onDismiss
      } = item;
      return {
        id,
        'aria-current': active ? 'page' : undefined,
        primary,
        secondary,
        active: !!active,
        dismissible: true,
        onClick,
        onDismiss,
        visual
      };
    });
  };

  return (
    <StyledNavCasesList>
      <NavigationList items={setupCases(cases)} />
    </StyledNavCasesList>
  );
};

export const Utils: FC<UtilsProps> = ({ operator, items, appHeader }) => {
  const { navOpen, navState } = useContext(AppShellContext);
  const [item, setItem] = useState<UtilItemProps>();
  const [drawerOpen, setDrawerOpen] = useState(false);

  useEffect(() => {
    if (drawerOpen && item) {
      setItem(
        items?.find<UtilItemProps>(
          (utilItem): utilItem is UtilItemProps =>
            !isValidElement(utilItem) && utilItem.name === item.name
        )
      );
    }
  }, [items]);

  const operatorInfo: NavListItemProps[] = useMemo(
    () =>
      appHeader
        ? []
        : [
            {
              primary: operator.name,
              visual: operator.avatar,
              items: operator.actions
            }
          ],
    [appHeader, operator.name, operator.avatar, operator.actions]
  );

  const transform = useCallback(
    (utilItem: UtilItemProps): NavListItemProps => {
      const uItem = { ...utilItem };
      delete uItem.onDrawerClose;
      delete uItem.onDrawerOpen;
      return {
        onClick: () => {
          if (utilItem.drawerView) {
            setItem(utilItem);
          }
        },
        ...uItem,
        primary: utilItem.name,
        visual: utilItem.count ? (
          <StyledUtilIconCount>
            {utilItem.visual}
            <Count as={StyledCountIcon} variant='urgent'>
              {utilItem.count}
            </Count>
          </StyledUtilIconCount>
        ) : (
          utilItem.visual
        ),
        collapseItems: !navOpen
      };
    },
    [navOpen]
  );

  const utilItems = useMemo(() => {
    return [
      ...(items ?? []).map(uItem => {
        return isValidElement(uItem) ? uItem : transform(uItem);
      }),
      ...operatorInfo
    ];
  }, [items, operatorInfo, transform]);

  useEffect(() => {
    if (item) setDrawerOpen(true);
  }, [item]);

  useEffect(() => {
    if (navState === 'closed' || navState === 'closing') setDrawerOpen(false);
  }, [navState]);

  return (
    <AppShellDrawerContext.Provider
      value={useMemo(
        () => ({
          drawerOpen,
          closeDrawer: () => {
            setDrawerOpen(false);
          },
          openDrawer: setItem
        }),
        [drawerOpen]
      )}
    >
      <StyledUtils>
        <NavigationList items={utilItems} collapsed />
      </StyledUtils>
      {item && (
        <AppShellDrawer
          drawerOpen={drawerOpen}
          setDrawerOpen={setDrawerOpen}
          content={item.drawerView}
          header={item.drawerHeader || item.name}
          onDrawerClose={() => {
            setItem(undefined);
            item.onDrawerClose?.();
          }}
          onDrawerOpen={item.onDrawerOpen}
        />
      )}
    </AppShellDrawerContext.Provider>
  );
};

const AppShell: FunctionComponent<AppShellProps & ForwardProps> = forwardRef(
  (props: PropsWithoutRef<AppShellProps>, ref: Ref<HTMLDivElement>) => {
    const {
      appInfo,
      searchInput,
      appHeader,
      searchPage,
      searchLabel = 'Sitewide', // FIXME: TR
      caseTypes,
      links,
      banners,
      environment: { name: envName, color: envColor } = {},
      main,
      operator,
      utils,
      cases,
      defaultExpanded = false,
      collapsedHoverMenus = false,
      ...restProps
    } = props;
    const {
      appName,
      portalName,
      href: appInfoHref,
      onClick: appInfoOnClick,
      imageSrc: logoSrc,
      appNameHidden,
      ...appInfoRestProps
    } = appInfo;

    let appInfoAs: ElementType = 'div';
    if (appInfoHref) appInfoAs = Link;
    else if (appInfoOnClick) appInfoAs = Button;

    const t = useI18n();
    const { start, end } = useDirection();
    const theme = useTheme();
    const navRef = useConsolidatedRef(ref);

    const [mobileNavOpen, setMobileNavOpen] = useState(false);
    const [mobileNavBackdrop, setMobileNavBackdrop] = useState(false);

    const focusedImperatively = useRef(false);
    const [headerEl, setHeaderEl] = useElement<HTMLDivElement>();
    const [mobileHeaderEl, setMobileHeaderEl] = useElement<HTMLDivElement>();
    const [appInfoEl, setAppInfoEl] = useElement<HTMLAnchorElement>();
    const [expandCollapseEl, setExpandCollapseEl] = useElement<HTMLButtonElement>();

    const isSmallOrAbove = useBreakpoint('sm');

    const showAppHeader = !!appHeader && isSmallOrAbove;
    const showMainContent = showAppHeader ? !!(headerEl || mobileHeaderEl) : true;

    const openNavTimeoutId = useRef<ReturnType<typeof setTimeout>>();
    const closeNavTimeoutId = useRef<ReturnType<typeof setTimeout>>();
    const previewTriggerRef = useRef<HTMLElement>(null);

    const [searchContainerEl, setSearchContainerEl] = useState<HTMLElement | null>(null);

    const { state: navState, set: setNavState } = useTransitionState({
      ref: navRef,
      property: 'width',
      states: openCloseStates,
      defaultState: defaultExpanded ? 'open' : 'closed'
    });

    const skipLinks: SkipLinksProps['items'] = useMemo(
      () => [
        {
          label: t('go_to_main_content'),
          target: { ariaLabel: t('main_content'), selector: 'main' }
        }
      ],
      [t]
    );

    const openNav = useCallback((delay: number = 0) => {
      if (closeNavTimeoutId.current) clearTimeout(closeNavTimeoutId.current);
      openNavTimeoutId.current = setTimeout(() => {
        setNavState('open');
      }, delay);
    }, []);

    const closeNav = useCallback((delay: number = 0) => {
      if (openNavTimeoutId.current) clearTimeout(openNavTimeoutId.current);
      closeNavTimeoutId.current = setTimeout(() => {
        setNavState('closed');
      }, delay);
    }, []);

    const onFocus = useCallback(() => {
      focusedImperatively.current = false;
    }, [openNav, isSmallOrAbove]);

    const onKeydown = useCallback(({ key }: KeyboardEvent) => {
      if (key === 'Escape') setMobileNavOpen(false);
    }, []);

    useEffect(() => {
      if (mobileNavOpen) {
        document.addEventListener('keydown', onKeydown);
        return () => {
          document.removeEventListener('keydown', onKeydown);
        };
      }
    }, [mobileNavOpen]);

    useEffect(() => {
      if (isSmallOrAbove && mobileNavOpen) {
        setMobileNavBackdrop(false);
        setMobileNavOpen(false);
      }
    }, [isSmallOrAbove, mobileNavOpen]);

    const appShellJSX = (
      <Flex
        {...restProps}
        container={{
          direction: 'column',
          justify: 'between'
        }}
        ref={isSmallOrAbove ? navRef : undefined}
        as={StyledAppNav}
        appHeader={showAppHeader}
        navOpen={mobileNavOpen}
        showEnv={!!envName}
        onFocus={onFocus}
      >
        {envName && (
          <StyledAppEnvironment
            item={{ shrink: 0 }}
            color={envColor ?? theme.base.colors.red['extra-light']}
          >
            {envName}
          </StyledAppEnvironment>
        )}

        {!showAppHeader && (
          <>
            <StyledAppInfo
              as={Grid}
              forwardedAs={appInfoAs}
              variant='link'
              href={appInfoHref}
              onClick={appInfoOnClick}
              aria-label={`${appName}${portalName ? ` - ${portalName}` : ''}`}
              {...appInfoRestProps}
              container={{
                alignItems: 'center',
                areas: portalName ? '"logo app" "logo portal"' : '"logo app"'
              }}
              ref={setAppInfoEl}
            >
              <Grid item={{ area: 'logo' }} as={Image} src={logoSrc} alt={appName} />
              <Grid item={{ area: 'app' }} as={appNameHidden ? VisuallyHiddenText : 'span'}>
                {appName}
              </Grid>
              {portalName && (
                <Grid item={{ area: 'portal' }} as={appNameHidden ? VisuallyHiddenText : 'span'}>
                  {portalName}
                </Grid>
              )}
            </StyledAppInfo>
            {navState === 'closed' && !appNameHidden && (
              <Tooltip
                target={appInfoEl}
                placement='right'
                as={StyledAppShellTooltip}
                showDelay='none'
                hideDelay='none'
              >
                {appName}
              </Tooltip>
            )}
          </>
        )}

        {searchInput && !showAppHeader && (
          <AppShellSearch
            searchLabel={searchLabel}
            collapsed={!mobileNavOpen && ['closed', 'closing'].includes(navState)}
            searchInput={searchInput}
          />
        )}

        <StyledScrollWrap
          onClick={
            isSmallOrAbove
              ? () => {
                  if (['closing', 'closed'].includes(navState)) {
                    openNav(0);
                  } else {
                    closeNav(0);
                  }
                }
              : undefined
          }
        >
          {caseTypes && caseTypes.length > 0 && <CaseTypes caseTypes={caseTypes} />}
          {links && <Links links={links} />}
          {cases && <Cases cases={cases} />}
        </StyledScrollWrap>

        <Utils appHeader={showAppHeader} operator={operator} items={utils} />
        {!mobileNavOpen && (
          <Flex container={{ justify: 'center' }}>
            <StyledAppShellToggleButton
              icon
              ref={setExpandCollapseEl}
              compact
              variant='simple'
              onClick={() => {
                setNavState(navState === 'closed' ? 'open' : 'closed');
              }}
              open={['open', 'opening'].includes(navState)}
              aria-label={navState === 'closed' ? t('expand') : t('collapse')}
            >
              {['closed', 'closing'].includes(navState) ? (
                <Icon name={`arrow-micro-${end}`} />
              ) : (
                <Icon name={`arrow-micro-${start}`} />
              )}
            </StyledAppShellToggleButton>
            {['open', 'closed'].includes(navState) && (
              <Tooltip
                target={expandCollapseEl}
                placement='right'
                as={StyledAppShellTooltip}
                showDelay='none'
                hideDelay='none'
              >
                {navState === 'closed' ? t('expand') : t('collapse')}
              </Tooltip>
            )}
          </Flex>
        )}
      </Flex>
    );

    return (
      <AppShellContext.Provider
        value={useMemo(
          () => ({
            navOpen: mobileNavOpen || navState === 'open' || navState === 'opening',
            collapsedHoverMenus,
            navState,
            focusedImperatively,
            headerEl: headerEl || mobileHeaderEl,
            previewTriggerRef,
            searchContainerEl,
            setSearchContainerEl,
            mobileNavOpen
          }),
          [navState, mobileNavOpen, headerEl, mobileHeaderEl, searchContainerEl]
        )}
      >
        <SkipLinks items={skipLinks} />
        {showAppHeader && (
          <AppHeader
            {...appInfo}
            searchInput={searchInput}
            searchLabel={searchLabel}
            operator={operator}
            searchPage={searchPage}
            ref={setHeaderEl}
          />
        )}

        {isSmallOrAbove ? (
          appShellJSX
        ) : (
          <>
            <Flex as={StyledMobileNav} container={{ alignItems: 'center' }} ref={setMobileHeaderEl}>
              <Button
                variant='simple'
                onClick={() => setMobileNavBackdrop(true)}
                aria-label={t('app_shell_open_nav')}
                icon
              >
                <Icon name='bars' />
              </Button>
            </Flex>

            <Backdrop
              open={mobileNavBackdrop}
              transitionSpeed='none'
              onAfterTransitionIn={() => setMobileNavOpen(true)}
              onBeforeTransitionOut={() => setMobileNavOpen(false)}
              onClick={e => {
                if (e.currentTarget === e.target) setMobileNavOpen(false);
              }}
            >
              <Drawer
                open={mobileNavOpen}
                onAfterClose={() => {
                  setMobileNavBackdrop(false);
                }}
                position='fixed'
                placement={start}
                size='min(18.75rem, calc(100vw - 3.5rem))'
                nullWhenClosed
              >
                {appShellJSX}
              </Drawer>
            </Backdrop>
          </>
        )}
        {showMainContent && (
          <StyledAppMain
            appHeader={showAppHeader}
            mobileNav={!isSmallOrAbove}
            headerOffset={isSmallOrAbove ? headerEl?.offsetHeight : mobileHeaderEl?.offsetHeight}
            navOpen={['open', 'opening'].includes(navState)}
          >
            {banners && <StyledBannerRegion>{banners}</StyledBannerRegion>}
            {main}
          </StyledAppMain>
        )}
      </AppShellContext.Provider>
    );
  }
);

export default AppShell;
